


















import {
  Component, Model, Prop, Vue,
} from 'vue-property-decorator';

@Component
export default class InputDropdown extends Vue {
  @Prop({ default: () => [] }) list!: string[];
  @Model('change') readonly selected!: string;

  isExpand = true;

  selectHandler(opt: string) {
    this.isExpand = true;
    this.$emit('change', opt);
  }

  get filterList() {
    return this.list.filter((d: string) => d !== this.selected);
  }
}
